import React ,{Component} from 'react';
import {Button,Modal} from 'antd-mobile';
import axios from 'axios';
import {testPORT} from '../../api/port';
import Scroll from 'react-bscroll';
class List extends Component{

    render(){
        let {data}=this.props;
        return <div style={{position:"relative",height:"620px"}}>
                <Scroll click={true}>
                    <ul className="userList">
                        {
                            data.length>0?data.map((item,key)=>{
                                return <li key={key}><span onClick={()=>{this.props.changeAmtend(true,item.name,item.id)}}></span> <span>{item.name}</span><span>{item.id}</span>
                                <Button
                                onClick={() =>
                                    Modal.alert('', '???', [
                                    { text: '', onPress: () => console.log('cancel') },
                                    { text: '', onPress: () => {
                                        axios.get(testPORT+"/remove",{params:{id:item.id}}).then((res)=>{
                                            if(res.data.code===1){
                                                this.props.disData(item.id)
                                                Modal.alert("","",[{text:"ok"}])
                                            }
                                        })
                                    } },
                                    ])
                                }
                                >
                                
                                </Button></li>
                            }):null
                        }
                    </ul>
                </Scroll>
        </div>
    }
}
export default List;


