import React ,{Component} from 'react';
import {Button,InputItem} from 'antd-mobile';
class AddUser extends Component {

    render (){
        let {markShow,id,name,money,addClick,changeAddName,changeAddMoney,changeAddId,changeCancle}=this.props;
        return   <div className="add" style={{display:markShow?"block":"none"}}>
                    <InputItem
                        type="number"
                        clear
                        value={id}
                        placeholder="ID"
                        onChange={(ev)=>{
                            changeAddId(ev)
                        }}
                    >ID</InputItem>
                    <InputItem
                        clear
                        value={name}
                        placeholder=""
                        onChange={(ev)=>{
                           changeAddName(ev)
                    }}
                    ></InputItem>
                    <InputItem
                        clear
                        value={money}
                        placeholder=""
                        onChange={(ev)=>{
                            changeAddMoney(ev)
                    }}
                    ></InputItem>
                    <Button className="btn" type="primary" inline size="small" style={{ marginRight: '4px' }} onClick={()=>{addClick()}}></Button>
                    <Button type="warning" inline size="small"  
                            onClick={()=>{
                                changeCancle(false,'')
                            }}
                    ></Button>
                </div>
    }
}
export default AddUser;


