import React ,{Component} from 'react';
class Header extends Component{

    render(){
        return   <header className="manngerHeader">
                        <span onClick={()=>{
                            this.props.history.go(-1)
                        }}></span>
                        <span></span>
                        <span onClick={()=>{this.props.addUserList(true)}}></span>
                </header>
    }
}
export default Header;


